/* Standardized glow effects */
.glow-orange-subtle {
  box-shadow: 0 4px 15px rgba(229, 95, 42, 0.3);
}

.glow-orange-medium {
  box-shadow: 0 8px 25px rgba(229, 95, 42, 0.4);
}

.glow-orange-strong {
  box-shadow: 0 12px 35px rgba(229, 95, 42, 0.5);
}

/* Simplified glow animation */
@keyframes glowPulse {
  0%, 100% { 
    box-shadow: 0 0 20px rgba(229, 95, 42, 0.4);
  }
  50% { 
    box-shadow: 0 0 30px rgba(229, 95, 42, 0.6);
  }
}

.glow-pulse {
  animation: glowPulse 2s ease-in-out infinite;
}

/* Typing indicator animation */
@keyframes typing {
  0%, 60%, 100% {
    transform: translateY(0);
    opacity: 0.7;
  }
  30% {
    transform: translateY(-10px);
    opacity: 1;
  }
}

.typing-dot {
  animation: typing 1.4s infinite;
}

.typing-dot:nth-child(2) {
  animation-delay: 0.2s;
}

.typing-dot:nth-child(3) {
  animation-delay: 0.4s;
}

/* Enhanced Custom scrollbar with smooth behavior */
.custom-scrollbar {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  will-change: scroll-position;
}

.custom-scrollbar::-webkit-scrollbar {
  width: 8px;
}

.custom-scrollbar::-webkit-scrollbar-track{
  border-radius: 9999px;
  --tw-bg-opacity: 1;
  background-color: rgb(31 41 55 / var(--tw-bg-opacity, 1));
}

.custom-scrollbar::-webkit-scrollbar-thumb{
  border-radius: 9999px;
  --tw-bg-opacity: 1;
  background-color: rgb(245 88 24 / var(--tw-bg-opacity, 1));
  -webkit-transition: background-color 0.2s ease;
  transition: background-color 0.2s ease;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(255 112 51 / var(--tw-bg-opacity, 1));
}

/* Chat-specific scrollbar with enhanced smoothness */
.chat-scrollbar {
  scroll-behavior: smooth;
  -webkit-overflow-scrolling: touch;
  will-change: scroll-position;
  transform: translateZ(0);
  scroll-padding-bottom: 160px;
}

.chat-scrollbar::-webkit-scrollbar {
  width: 6px;
}

.chat-scrollbar::-webkit-scrollbar-track{
  border-radius: 9999px;
  background-color: rgb(31 41 55 / 0.5);
}

.chat-scrollbar::-webkit-scrollbar-thumb{
  border-radius: 9999px;
  background-color: rgb(245 88 24 / 0.8);
  -webkit-transition: all 0.2s ease;
  transition: all 0.2s ease;
}

.chat-scrollbar::-webkit-scrollbar-thumb:hover{
  --tw-bg-opacity: 1;
  background-color: rgb(255 112 51 / var(--tw-bg-opacity, 1));
}

/* Connection status indicator */
@keyframes pulse-dot {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.5);
    opacity: 0.5;
  }
}

.status-indicator {
  animation: pulse-dot 2s infinite;
}

/* Smooth transitions for all interactive elements */
button, a, input, textarea, .interactive{
  transition-property: all;
  transition-duration: 200ms;
  transition-timing-function: cubic-bezier(0.4, 0, 0.2, 1);
}

/* Focus states */
button:focus-visible, a:focus-visible, input:focus-visible{
  outline: 2px solid transparent;
  outline-offset: 2px;
  --tw-ring-offset-shadow: var(--tw-ring-inset) 0 0 0 var(--tw-ring-offset-width) var(--tw-ring-offset-color);
