@media (max-width: 768px) {
  .hidden-mobile {
    display: none !important;
  }

  #root {
    padding: 0 !important;
    height: 100vh !important;
    overflow: hidden !important;
  }

  .chat-input {
    font-size: 16px !important;
  }

  .full-chat {
    height: 100vh !important;
  }

  body {
    height: 100vh !important;
    overflow: hidden !important;
    -webkit-overflow-scrolling: touch !important;
  }

  .chat-scrollbar {
    -webkit-overflow-scrolling: touch;
    scroll-snap-type: none;
    overscroll-behavior: contain;
  }

  .scroll-to-bottom-inside{
    bottom: 7rem;
    right: 0.75rem;
    height: 2.5rem;
    width: 2.5rem;
  }
}

/* Widget Mode Styles */
.widget-mode {
  width: 100%;
  height: 100%;
  max-width: 384px;
  max-height: 500px;
  overflow: hidden;
  position: relative;
}

.widget-wrapper {
  width: 100%;
  height: 100vh;
  max-height: 500px;
  background: transparent;
  display: flex;
  flex-direction: column;
}

.widget-chat {
  height: 100%;
  display: flex;
  flex-direction: column;
  position: relative;
}

.full-chat {
  height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
  overflow: hidden;
}

/* Full Page Mode Styles */
.fullpage-mode {
  min-height: 100vh;
  width: 100%;
}

/* Responsive widget mode for mobile */
@media (max-width: 480px) {
  .widget-mode {
    max-width: 100vw;
    max-height: 100vh;
    width: 100vw;
    height: 100vh;
  }

  .widget-mode body {
    height: 100vh !important;
    overflow: hidden !important;
  }

  .widget-mode .message-bubble {
    max-width: 95%;
    font-size: 14px;
  }

  .widget-mode .chat-input {
    font-size: 16px !important;
  }
}
